import { Component, OnInit, OnDestroy, AfterViewInit } from "@angular/core";
import { DialogModule, CalendarModule } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { Observable, Subscription } from 'rxjs/Rx';

//import { PanelMenuService } from './panelmenu.service';

@Component({
    selector: 'report-view',
    template: require('./reportview.component.html'),
    //styles: [require('./panelmenu.component.css')],
    //providers: [PanelMenuService]
})
export class ReportViewComponent implements OnInit {
    private id: string;
    private sub: Subscription;

    public display: boolean = false;
    fromDate: Date;
    toDate: Date;

    constructor(private route: ActivatedRoute) {
        this.sub = this.route.params.subscribe(params => {
            this.id = params['id'];
            this.showDialog();
        });
    }

    public showDialog() {
        this.display = true;
    }

    ngOnInit() { }

    AfterViewInit() { }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }

}


